/*
XXX used variables: only for the IDE, will not really used by the build system, because this
XXX code will be inserted after the _variables.scss
*/
/* used icons ---------------------------------------------------- */
/* non-bootstrap variabled --------------------------------------- */
/* badge -------------------------------------------------------------- */
.badge.btn {
  border: 0;
}

.btn-group > .tobago-badge.badge.btn {
  z-index: initial;
  cursor: initial;
}

/* bar -------------------------------------------------------------- */
.tobago-bar.navbar-light .navbar-brand > .tobago-link {
  color: #ffb243;
}

.tobago-bar.navbar-light .navbar-brand > .tobago-link:hover, .tobago-bar.navbar-light .navbar-brand > .tobago-link:focus {
  text-decoration: none;
}

.tobago-bar.navbar-dark .navbar-brand > .tobago-link {
  color: #fff;
}

.tobago-bar.navbar-dark .navbar-brand > .tobago-link:hover, .tobago-bar.navbar-dark .navbar-brand > .tobago-link:focus {
  text-decoration: none;
}

/* box -------------------------------------------------------------- */
.tobago-box {
  margin-bottom: 0.75rem;
}

.tobago-box > .card-body {
  overflow-x: hidden;
  overflow-y: auto;
}

.tobago-box-header {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.card-header > h3:first-child {
  margin-bottom: 0;
  align-self: center;
}

.card-header .navbar {
  padding: 0;
}

/* buttons, links ------------------------------------------------------- */
.tobago-buttons > .tobago-button {
  display: inline-block;
}

/* button, link ------------------------------------------------------- */
.tobago-button > :nth-child(n+2), .tobago-link > :nth-child(n+2) {
  margin-left: .4em;
}

/* button -------------------------------------------------------------- */
a.tobago-button.btn-primary:focus {
  color: #fff;
}

a.tobago-button.btn-secondary:focus {
  color: colors("gray-dark");
}

button.tobago-button:disabled {
  cursor: not-allowed;
}

button.tobago-button:disabled img, button.tobago-button:disabled span {
  pointer-events: none;
}

/* collapsible -------------------------------------------------------------- */
.tobago-collapsed.tobago-box .card-body,
.tobago-collapsed.tobago-section .tobago-section-content,
.tobago-collapsed.tobago-panel {
  display: none;
}

/* date -------------------------------------------------------------- */
.tobago-date {
  min-width: 7em;
}

.tobago-date::-ms-clear {
  /* Remove IE10's "clear field" X button */
  display: none;
}

.tobago-date:disabled {
  color: rgba(160, 160, 160, 0.5);
}

/* for pickers with more than one icon, e.g. date-time picker */
.btn > .fa:nth-child(n+2) {
  margin-left: .3em;
}

/*
XXX workaround for Bootstrap with datetimepicker needed for popups
*/
.bootstrap-datetimepicker-widget {
  z-index: 2000 !important;
}

/* figure -------------------------------------------------------------- */
/* file -------------------------------------------------------------- */
.tobago-file {
  position: relative;
}

.tobago-file-pretty {
  text-overflow: ellipsis;
}

.tobago-file-real {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  width: 100%;
  z-index: 2;
}

/* flexLayout -------------------------------------------------------------- */
.tobago-flexLayout {
  min-width: 0;
  /* without this, Firefox/Webkit are different from IE:
     Set the minimal width to zero make flex-layout responsive for the width,
     even if there is overflowing content.
     currently e.g. PRE-code blocks in the demo are too wide. XXX */
  margin-left: -3px;
  margin-right: -3px;
}

.tobago-flexLayout > * {
  margin-left: 3px;
  margin-right: 3px;
}

.tobago-flexLayout > .tobago-flexLayout {
  margin-left: 0;
  margin-right: 0;
}

/* the non-label-element inside of a label-layout with flex box */
.tobago-flexLayout.tobago-label-container > .form-control, .tobago-flexLayout.tobago-label-container > .form-control-plaintext, .tobago-flexLayout.tobago-label-container > .twitter-typeahead,
.tobago-flexLayout.tobago-label-container > .tobago-input-group-outer, .tobago-flexLayout.tobago-label-container > .tobago-messages-container,
.tobago-flexLayout.tobago-label-container > .tobago-file, .tobago-flexLayout.tobago-label-container > .tobago-selectManyShuttle {
  flex: 1 0 0px;
}

/* flowLayout ---------------------------------------------------------- */
/* footer -------------------------------------------------------------- */
.tobago-footer {
  background-color: #ffffff;
  padding: 5px 10px;
  box-shadow: 0 0 5px 5px rgba(200, 200, 200, 0.5);
}

.fixed-bottom {
  margin-top: 10px;
}

/* form -------------------------------------------------------------- */
/* gridLayout -------------------------------------------------------------- */
.tobago-gridLayout {
  display: -ms-grid;
  display: grid;
  border-spacing: 0;
  border-collapse: collapse;
  grid-column-gap: 1rem;
  column-gap: 1rem;
}

.tobago-gridLayout .tobago-label-container > .tobago-textarea, .tobago-gridLayout .tobago-label-container > .tobago-selectManyListbox, .tobago-gridLayout .tobago-label-container > .tobago-selectManyShuttle {
  height: 100%;
}

/* header ----------------------------------------------------------- */
.tobago-header {
  margin-bottom: 1rem;
}

.tobago-header.fixed-top {
  margin-bottom: 0;
}

/* hidden ----------------------------------------------------------- */
.tobago-hidden:disabled {
  display: none;
}

/* image ----------------------------------------------------------- */
.tobago-image.disabled {
  filter: grayscale(1) blur(2px) contrast(0.5) brightness(1.2);
}

/* in ----------------------------------------------------------- */
.tobago-in:disabled {
  color: rgba(160, 160, 160, 0.5);
}

.tobago-in-markup-number {
  text-align: right;
}

/* label ----------------------------------------------------------- */
.tobago-label {
  width: 100%;
}

.tobago-flexLayout > .tobago-label {
  width: 155px;
}

/* link ----------------------------------------------------------- */
.tobago-link {
  white-space: nowrap;
}

.tobago-link span {
  white-space: normal;
}

.tobago-link.nav-link:focus, .tobago-link.nav-link:hover {
  text-decoration: none;
}

a.tobago-link.dropdown-item img, a.tobago-link.dropdown-item span {
  display: inline-block;
  /* fix for IE11 - :active selector doesn't work for nested elements */
  pointer-events: none;
}

h1 button.tobago-link, h2 button.tobago-link, h3 button.tobago-link, h4 button.tobago-link, h5 button.tobago-link, h6 button.tobago-link {
  font-weight: inherit;
}

button.tobago-link, button.tobago-treeCommand {
  color: #130E8F;
  border-width: 0;
  padding: 0;
  background-color: transparent;
  text-align: left;
}

button.tobago-link:focus, button.tobago-link:hover, button.tobago-treeCommand:focus, button.tobago-treeCommand:hover {
  color: #dbb2ff;
  cursor: pointer;
  outline: none;
  text-decoration: underline;
}

button.tobago-link:disabled, button.tobago-treeCommand:disabled {
  color: #6c757d;
}

button.tobago-link:disabled:hover, button.tobago-treeCommand:disabled:hover {
  cursor: not-allowed;
  text-decoration: none;
}

button.tobago-link:disabled > img, button.tobago-treeCommand:disabled > img {
  opacity: .65;
}

button.tobago-link.dropdown-item {
  padding: 0.25rem 1.5rem;
  color: #a0a0a0;
  text-align: inherit;
  white-space: nowrap;
}

button.tobago-link.dropdown-item:active {
  color: #ffb243;
}

button.tobago-link.dropdown-item:focus, button.tobago-link.dropdown-item:hover {
  cursor: pointer;
  text-decoration: none;
}

button.tobago-link.dropdown-item:disabled {
  color: #6c757d;
}

button.tobago-link.dropdown-toggle:focus, button.tobago-link.dropdown-toggle:hover {
  text-decoration: none;
}

button.nav-link {
  /* bootstrap don't know button.nav-link*/
  padding: 0.5em 1em;
  padding-top: .55em;
  padding-bottom: .45em;
}

.navbar-nav button.nav-link {
  padding-left: 0;
  padding-right: 0;
}

span.dropdown {
  display: inline-block;
}

.dropdown > .btn.dropdown-toggle {
  width: 100%;
}

/* messages / help text ----------------------------------------------- */
.tobago-messages-container {
  align-items: flex-start;
}

.tobago-messages-container.tobago-flexLayout .tobago-messages:first-child small label {
  margin-right: 0;
  /* for tc:in margin 5px is already set */
  margin-bottom: 5px;
}

a.tobago-messages-button, a.tobago-help-button {
  padding-left: 0.4em;
  padding-right: 0.4em;
}

.tobago-messages label {
  display: block;
}

.tobago-messages label:last-child {
  margin-bottom: 0;
}

.tobago-flexLayout.tobago-messages-container > .twitter-typeahead, .tobago-flexLayout.tobago-messages-container > .tobago-input-group-outer, .tobago-flexLayout.tobago-messages-container > .tobago-selectManyShuttle, .tobago-flexLayout.tobago-messages-container > .tobago-file {
  flex: 1 0 0px;
}

.tobago-messages .alert.alert-dismissible {
  padding-left: 3rem;
}

.tobago-messages .alert.alert-dismissible:before {
  font-family: FontAwesome;
  position: absolute;
  left: 0;
  padding-left: 1rem;
  padding-right: 1rem;
  width: 3rem;
  text-align: center;
}

.tobago-messages .alert.alert-dismissible.alert-danger:before {
  content: "";
}

.tobago-messages .alert.alert-dismissible.alert-warning:before {
  content: "";
}

.tobago-messages .alert.alert-dismissible.alert-info:before {
  content: "";
}

.popover .popover-body {
  white-space: pre-line;
}

/* nav ----------------------------------------------------------- */
/* styles for drop down menu first level */
.dropdown-toggle:focus, .dropdown-toggle:hover {
  text-decoration: none;
}

/* no bottom border in navtabs, if panel-body under it */
.nav-tabs {
  border-bottom-width: 0;
}

.nav-tabs > li.active > a {
  background-image: linear-gradient(to bottom, #f5f5f5 0%, #e8e8e8 100%);
  background-repeat: repeat-x;
  border-bottom: #dddddd;
}

.nav-tabs > li.active > a:hover {
  border-bottom: #dddddd;
}

.dropdown-menu .form-check {
  display: block;
}

.dropdown-menu .custom-control {
  padding-left: 0;
}

.dropdown-menu .custom-control .custom-control-label.dropdown-item {
  padding-left: 3rem;
}

.dropdown-menu .custom-control .custom-control-label.dropdown-item:before {
  top: 0.5rem;
  left: 1.5rem;
}

.dropdown-menu .custom-control .custom-control-label.dropdown-item:after {
  top: 0.5rem;
  left: 1.5rem;
}

/* dropdown-submenu ------------------------------------------------------- */
.tobago-dropdown-submenu {
  cursor: pointer;
  position: relative;
  width: 100%;
}

.tobago-dropdown-submenu:active > button.tobago-link {
  color: #ffb243;
}

.tobago-dropdown-submenu > .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
  border-radius: 6px 6px 6px 6px;
}

.tobago-dropdown-submenu > button:after {
  display: none;
}

.tobago-dropdown-submenu > button.tobago-link {
  color: #a0a0a0;
}

.tobago-dropdown-submenu > button.tobago-link:focus, .tobago-dropdown-submenu > button.tobago-link:hover {
  text-decoration: none;
}

.tobago-dropdown-submenu:after {
  content: " ";
  float: right;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 0 5px 5px;
  border-left-color: #cccccc;
  margin-top: 7px;
  margin-right: -10px;
}

.tobago-dropdown-submenu:hover > .dropdown-menu {
  display: block;
}

.tobago-dropdown-submenu:hover > .dropdown-menu > a:after {
  border-left-color: #000000;
}

.tobago-dropdown-submenu.pull-left {
  float: none;
}

.tobago-dropdown-submenu.pull-left > .dropdown-menu {
  left: -100%;
  margin-left: 10px;
  border-radius: 6px 0 6px 6px;
}

/* object ----------------------------------------------------------------- */
/* out -------------------------------------------------------------------- */
.tobago-out {
  display: inline-block;
}

.tobago-out-markup-strong {
  font-weight: bold;
}

.tobago-out-markup-deleted {
